<template>
  <div class="cart-family-bundle__line-item-wrapper">
    <a
      class="ajax-cart__image responsive-image__wrapper knockout-image"
      :href="`/${bundle.handle}`"
    >
      <img
        :alt="variant.title"
        sizes="auto"
        :src="getImageUrl(getVariantImage, '50x')"
        :srcset="`
          ${getImageUrl(getVariantImage, '120x')} 120w,
          ${getImageUrl(getVariantImage, '240x')} 240w,
          ${getImageUrl(getVariantImage, '320x')} 320w
        `"
      >
    </a>

    <div class="cart-family-bundle__description">
      <div class="cart-family-bundle__inner">
        <a
          class="cart-family-bundle__title subtitle-2"
          :href="`/${bundle.handle}`"
          v-text="bundle.title"
        />

        <span
          class="cart-family-bundle__variant-title"
          v-text="variant.title"
        />

        <product-price-message
          v-if="hasPricingMessage"
          :lowest-price="lowestPrice"
          :recommended-price="recommendedPrice"
        />
        <div class="ajax-cart__price body-2">
          <span
            class="visually-hidden"
            v-text="$string('cart.label.original_price')"
          />

          <span
            class="ajax-cart__price-current"
            :class="{'sale': recommendedPrice}"
            v-text="$formatMoney(variant.price.amount)"
          />
          <span
            v-if="getPercentText"
            class="badge badge--primary"
            v-text="getPercentText"
          />
        </div>
      </div>

      <button
        class="cart-family-bundle__add text-link"
        tabindex="0"
        :data-cart-add="variant.id"
        @click.prevent="handleAddToCart(bundle)"
      >
        <span v-text="`+ ${$string('product_family_bundles.cart.cta')}`" />
        <span
          class="is-hidden"
          v-text="$string('product.cta.add_to_cart_limit')"
        />
      </button>
    </div>
  </div>
</template>

<script>

/**
 * Vue: Cart Family Bundle Slide (cart-family-bundle-slide)
 * -----------------------------------------------------------------------------
 * Cart family bundle slide with swap function
 *
 */

import { getSizedImageUrl } from '~/helpers/format'
import { mapActions } from 'vuex'
import timings from '~/config/timings'
import ProductPriceMessage from '~global/product-price-message/product-price-message'

export default {
  name: 'CartFamilyBundleSlide',

  components: {
    ProductPriceMessage
  },

  props: {
    bundle: {
      type: Object,
      default: () => ({}),
    },
    lineItem: {
      type: Object,
      default: () => ({}),
    },
  },

  data() {
    return {
      selectedVariant: this.bundle.variants[0]?.id || null,
    }
  },

  computed: {

    /**
     * Get variant from product bundle that matches line item variant.
     * @returns {Object}
     */
    variant() {
      console.log('getVariant family lineItem ====>>>', this.lineItem)
      console.log('this.lineItem.variant =>', this.lineItem.variant)
      const variantAvailable = this.bundle.variants.find((bundleVariant) => {
        return bundleVariant.available === true &&
          this.lineItem.variant_title.toLowerCase() === bundleVariant.title.toLowerCase()
      })

      console.log('variantAvailable', variantAvailable)

      return variantAvailable || this.bundle.variants[0]
    },

    /**
     * Get variant image
     */
    getVariantImage() {
      const variantImage = this.bundle.images.find((image) => {
        return image.id === this.variant.image
      }) || this.bundle.featuredImage

      return variantImage.src
    },

    /**
     * Get product lowest price.
     * @returns {Number}
     */
    lowestPrice() {
      if (!this.variant?.lowestPrice) {
        return false
      }

      const sanitizedLowestPrice = this.variant.lowestPrice
        .replace(/_DELETE_/g, '')

      return sanitizedLowestPrice.length > 0
        ? sanitizedLowestPrice
        : false
    },

    /**
     * Get product recommended price.
     * @returns {Number}
     */
    recommendedPrice() {
      return this.variant.compareAtPrice.amount > this.variant.price.amount
        ? this.$formatMoney(this.variant.compareAtPrice.amount)
        : false
    },

    /**
     * Get percentage discount text.
     * @returns {String}
     */
     getPercentText() {
      const { compareAtPrice, price } = this.variant
      if (!compareAtPrice.amount || compareAtPrice.amount <= price.amount) {
        return false
      }

      const discountPercentage = 100 - Math.floor(((price.amount / compareAtPrice.amount) * 100));
      if (discountPercentage <= 0) {
        return false
      }

      const saleText = this.$string('layout.general.sale').replace('{{amount}}', discountPercentage)
      return saleText
    },

    /**
     * Get price message
     * @returns {String}
     */
    hasPricingMessage() {
      const enabled = this.$variable('settings.pricingDirective.enable')

      if (!enabled) {
        return false
      }

      return enabled
    },
  },

  mounted() {
    console.log('cart family-bundle mount--->', this.bundle)
  },

  methods: {

    /**
     * Map Vuex actions.
     */
    ...mapActions({
      addItem: 'cart/addItem',
    }),

    /**
     * Handle add to cart submission.
     */
    async handleAddToCart() {
      clearTimeout(this.timeout)
      const properties = {}
      this.error = ''
      this.state = 'adding'
      cnvs.EventBus.emit('family-bundle-updating')

      if (cnvs.storybook) {
        this.timeout = setTimeout(() => {
          this.state = 'added'
        }, timings.slow)

        this.timeout = setTimeout(() => {
          this.state = 'ready'
        }, timings.message)

        return
      }

      properties._undoProperties = this.lineItem.properties
      properties._undoProperties._undoId = this.lineItem.id
      properties._lowestPrice = this.variant.lowestPrice
      properties._recommendedPrice = this.variant.compareAtPrice

      const items = [
        {
          id: this.variant.id,
          properties,
          quantity: this.lineItem.quantity,
        },
      ]

      try {
        await this.addItem(items)

        this.state = 'added'

        cnvs.EventBus.emit('family-bundle-remove', {key: this.lineItem.key})

        this.timeout = setTimeout(() => {
          this.state = 'ready'
        }, timings.message)

      } catch (error) {
        this.state = 'error'

        if (error.length) {
          this.error = error
          return
        }

        reportError('Failed to add product to cart', error)
      }
    },

    /**
     *
     * @param {String} src - variant image src
     * @param {String} size - size for image
     * @returns {String}
     */
    getImageUrl(src, size) {
      return getSizedImageUrl(src, size)
    },
  },
}
</script>
