<template>
  <bundle-accordion
    class="cart-family-bundle"
    :section="`cartFamilyBundles${index}`"
  >
    <template #header>
      <div
        class="cart-family-bundle__control accordion__control subtitle-1"
        aria-controls="bundleCartDrawerContent"
        aria-expanded="false"
      >
        <span>
          <icon-tag />
        </span>

        <div class="cart-family-bundle__heading">
          <span
            class="cart-family-bundle__heading-title"
            v-text="percentageText"
          />
          <p
            class="cart-family-bundle__heading-description"
            v-text="$string('product_family_bundles.cart.description')"
          />
        </div>

        <div
          class="cart-family-bundle__cta"
          @click="initCarousel()"
        >
          <span
            class="cart-family-bundle__cta--chevron"
          >
            <icon-chevron-down />
          </span>
        </div>
      </div>
    </template>

    <template #item>
      <div
        id="bundleCartDrawerContent"
        class=" cart-family-bundle__container"
        role="region"
        aria-labelledby="bundleCartDrawerAccordion1"
      >
        <div>
          <button
            v-if="bundles.length > 0"
            class="swiper-button-prev swiper-button-custom"
            js-cart-family-bundle-carousel="prev"
          >
            <icon-chevron-left />
          </button>

          <div
            id="BundleCartCarousel-0"
            class="cart-family-bundle__carousel"
          >
            <swiper
              ref="bundleCarousel"
              class="cart-family-bundle__slide-wrap"
              :modules="modules"
              :breakpoints="swiper.breakpoints"
              :navigation="swiper.navigation"
              :observer="true"
              :observe-parents="true"
              :prevent-interaction-on-transition="true"
              :slides-per-view="1"
              :watch-overflow="true"
            >
              <swiper-slide
                v-for="(bundle, index) in bundles"
                :key="`cart-family-bundle-${index}`"
                class="cart-family-bundle__line-item swiper-slide"
                data-index="${index}"
                data-product-id="${item.id}"
                data-next-slide="${window.theme.strings.accessibility.nextSlideMessage}"
                data-prev-slide="${window.theme.strings.accessibility.prevSlideMessage}"
              >
                <cart-family-bundle-slide
                  :bundle="bundle"
                  :line-item="lineItem"
                />
              </swiper-slide>
            </swiper>
          </div>

          <button
            v-if="bundles.length > 0"
            class="swiper-button-next swiper-button-custom"
            js-cart-family-bundle-carousel="next"
          >
            <icon-chevron-right />
          </button>
        </div>
      </div>
    </template>
  </bundle-accordion>
</template>

<script>

/**
 * Vue: Cart family bundle (cart-family-bundle)
 * -----------------------------------------------------------------------------
 * Cart family bundle component to be used in cart drawer and cart page.
 *
 */
import { Swiper, SwiperSlide } from 'swiper/vue'
import { Navigation } from 'swiper'

import BundleAccordion from '~async/bundle-accordion/bundle-accordion'
import CartBundleSlide from './cart-family-bundle-slide'
import CartFamilyBundleSlide from './cart-family-bundle-slide'
import IconChevronDown from '~icons/direction/chevron/down.svg'
import IconChevronLeft from '~icons/direction/chevron/left.svg'
import IconChevronRight from '~icons/direction/chevron/right.svg'
import IconTag from '~icons/misc/tag.svg'

export default {
  name: 'CartFamilyBundle',

  components: {
    BundleAccordion,
    CartBundleSlide,
    CartFamilyBundleSlide,
    IconChevronDown,
    IconChevronLeft,
    IconChevronRight,
    IconTag,
    Swiper,
    SwiperSlide,
  },

  props: {
    bundles: {
      type: Array,
      default: () => ([]),
    },
    lineItem: {
      type: Object,
      default: () => ({}),
    },
    nameSpace: {
      type: String,
      default: '',
    },
    indexindex: {
      type: Number,
      default: 0,
    },
  },

  setup() {
    return {
      modules: [Navigation],
    }
  },

  computed: {

    /**
     * Compute percentage text
     */
    percentageText() {
      let saleText = this.$string('product_family_bundles.cart.title')
      const { compareAtPrice, price } = this.bundles[0]
        if (!compareAtPrice.amount || compareAtPrice.amount <= price.amount) {
          saleText = saleText.split('{{amount}}')[0]
          return saleText
        }

        const discountPercentage = 100 - Math.floor(((price.amount / compareAtPrice.amount) * 100));
        if (discountPercentage <= 0) {
          saleText = saleText.split('{{amount}}')[0]
          return saleText
        }

        saleText = saleText.replace('{{amount}}', discountPercentage)
        return saleText
    }
  },

  data() {
    return {
      swiper: {
        breakpoints: {
          1280: {
            slidesPerView: 1,
            spaceBetween: 18,
          },
        },
        navigation: {
          nextEl: '[js-cart-family-bundle-carousel="next"]',
          prevEl: '[js-cart-family-bundle-carousel="prev"]',
        },
      },
    }
  },

  methods: {
    initCarousel() {
      this.$refs.bundleCarousel.update()
    },
  },
}
</script>

<style lang="scss">
@import '~/../styles/components/badge';
@import './cart-family-bundle';
</style>
